  font-weight: 800;
  font-size: 54px;
  line-height: 156%;
  font-family: "Alumni Sans", "Arial", sans-serif;
  letter-spacing: 1.62px;
}
@media (max-width: 1199px) {
  .lesson__title {
    margin: 0 0 30px;
    font-weight: 700;
    font-size: 42px;
    line-height: normal;
    letter-spacing: 1.26px;
  }
}
@media (max-width: 767px) {
  .lesson__title {
    margin: 0 0 32px;
    font-weight: 600;
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.lesson__inputs-container {
  display: grid;
  margin: 0 0 29px;
  gap: 30px;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 1199px) {
  .lesson__inputs-container {
    gap: 30px 60px;
  }
}
@media (max-width: 767px) {
  .lesson__inputs-container {
    margin: 0 0 38px;
    grid-template-columns: 1fr;
  }
}

.lesson__button {
  width: 100%;
  padding: 16px 20px 15px;
  font-size: 14px;
  line-height: normal;
  letter-spacing: 0.7px;
}
@media (max-width: 1199px) {
  .lesson__button {
    padding: 16px 20px 15px;
  }
}
@media (max-width: 767px) {
  .lesson__button {
    padding: 16px 20px 15px;
  }
}

.custom-input.is-invalid .custom-input::before {
  background-color: #ed0233;
}

.custom-input:has(input[disabled]) {
  pointer-events: none;
}
.custom-input:has(input[disabled])::before {
  opacity: 0.3;
}

.custom-input label {
  position: relative;
  display: flex;
  flex-direction: column;
}
.custom-input label::before {
  content: "";
  position: absolute;
  bottom: 1px;
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  opacity: 0.5;
  transition: opacity 0.3s ease;
}
.custom-input label:hover:not([disabled])::before {
  opacity: 1;
}
.custom-input label:focus-within:not([disabled])::before {
  top: -1px;
  left: -5px;
  z-index: 1;
  width: calc(100% + 10px);
  height: 100%;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #ffffff;
  opacity: 1;
  animation: opacity 0.3s ease;
}
.custom-input label:active:not([disabled])::before {
  top: -1px;
  left: -5px;
  z-index: 1;
  width: calc(100% + 10px);
  height: 100%;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #ffffff;
  opacity: 1;
  animation: opacity 0.3s ease;
}
.custom-input label[disabled] {
  opacity: 0.3;
  pointer-events: none;
}

.custom-input__label {
  position: fixed;
  transform: scale(0);
}

.custom-input input {
  padding: 5px 0;
  font-size: 14px;
  line-height: 150%;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  outline: none;
  cursor: text;
}
.custom-input input::placeholder {
  color: #ffffff;
}
.custom-input input:focus::-webkit-input-placeholder, .custom-input input:active::-webkit-input-placeholder {
  color: transparent;
}

.custom-input .is-invalid {
  display: flex;
  font-size: 14px;
  line-height: 150%;
  color: #ff121f;
}

.contacts {
  position: relative;
}
.contacts::before {
  content: "";
  position: absolute;
  right: -50vw;
  left: 0;
  z-index: -1;
  height: 1px;
  background-color: #74819c;
}
@media (max-width: 1199px) {
  .contacts::before {
    content: none;
  }
}

.contacts__container {
  padding: 58px 0 64px 121px;
}
@media (max-width: 1199px) {
  .contacts__container {
    padding: 62px 88px 61px;
  }
}
@media (max-width: 767px) {
  .contacts__container {
    padding: 64px 0 62px;
  }
}

.contacts__title {
  margin: 0 0 36px;
}
@media (max-width: 1199px) {
  .contacts__title {
    margin: 0 0 31px;
  }
}
@media (max-width: 767px) {
  .contacts__title {
    margin: 0 0 33px;
  }
}

.contacts__list {
  position: relative;
  display: grid;
  margin: 0;
  padding: 0;
  padding-left: 3px;
  list-style-type: none;
  font-style: normal;
  gap: 18px 5px;
  grid-template-columns: repeat(2, 1fr);
}
.contacts__list::before {
  content: "";
  position: absolute;
  top: 6px;
  left: calc(51% - 1px);
  width: 1px;
  height: calc(100% - 12px);
  margin: 0 auto;
  background-color: #74819c;
}
@media (max-width: 1199px) {
  .contacts__list {
    padding-left: 0;
    gap: 19px 32px;
  }
  .contacts__list::before {
    left: 53%;
  }
}
@media (max-width: 767px) {
  .contacts__list {
    gap: 19px 0;
    grid-template-columns: 1fr;
  }
  .contacts__list::before {
    content: none;
  }
}

.contacts__item {
  margin: 0 0 0 59px;
}
.contacts__item--left {
  margin: 0 55px 0 0;
}
@media (max-width: 1199px) {
  .contacts__item {
    margin: 0 0 0 20%;
  }
  .contacts__item--left {
    margin: 0 15px 0 0;
  }
}
@media (max-width: 767px) {
  .contacts__item {
    margin: 0;
  }
  .contacts__item--phone {
    grid-area: 3/1/4/-1;
  }
  .contacts__item--schedule {
    grid-area: 2/1/3/-1;
  }
  .contacts__item--email {
    margin: -1px 0 0;
  }
}

.contacts__subtitle {
  margin: 0 0 3px;
  font-weight: 700;
  line-height: 150%;
}
@media (max-width: 1199px) {
  .contacts__subtitle {
    margin: 0 0 6px;
    font-size: 20px;
  }
}

.contacts__text {
  margin: 0;
  font-size: 14px;
  line-height: 152%;
}
@media (max-width: 1199px) {
  .contacts__text {
    font-size: 18px;
  }
}

.footer {
  background-color: #030a1a;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 41px;
  padding-bottom: 42px;
}
@media (max-width: 1199px) {
  .footer__container {
    padding-top: 45px;
    padding-bottom: 44px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    position: relative;
    flex-direction: column;
    align-items: stretch;
    padding-top: 48px;
    padding-bottom: 52px;
  }
}

.footer__logo-link {
  margin: 0 0 -11px;
  transition: opacity 0.3s ease;
}
@media (max-width: 1199px) {
  .footer__logo-link {
    margin: -3px 0 0;
  }
}
@media (max-width: 767px) {
  .footer__logo-link {
    position: absolute;
    z-index: 1;
    margin: 4px 0 0 1px;
  }
}
.footer__logo-link:hover:not([disabled]) {
  outline: none;
  opacity: 0.5;
}
.footer__logo-link:focus-visible:not([disabled]) {
  outline: none;
  opacity: 0.5;
}
.footer__logo-link:active:not([disabled]) {
  opacity: 0.7;
}

.footer__logo-link img {
  width: 86px;
  height: 17px;
}
@media (max-width: 1199px) {
  .footer__logo-link img {
    width: 79px;
    height: 16px;
  }
}
@media (max-width: 767px) {
  .footer__logo-link img {
    width: 65px;
    height: 13px;
  }
}

.footer__list {
  display: flex;
  flex-wrap: wrap;
  max-width: 470px;
  margin: 0 15px;
  padding: 0;
  list-style-type: none;
  transform: translateX(6px);
  gap: 11px 40px;
}
@media (max-width: 1199px) {
  .footer__list {
    transform: translateX(9px);
    gap: 11px 30px;
  }
}
@media (max-width: 767px) {
  .footer__list {
    display: grid;
    max-width: 100%;
    margin: 0 0 35px;
    transform: translateX(0);
    gap: 21px 30px;
    grid-template-columns: repeat(2, 1fr);
  }
  .footer__list::before {
    content: "";
    grid-area: 1/1/2/2;
  }
}

@media (max-width: 767px) {
  .footer__item--services {
    order: -2;
  }
  .footer__item--stocks {
    order: -3;
  }
  .footer__item--trainers {
    order: -1;
  }
}

.footer__link {
  position: relative;
  font-size: 14px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  outline: none;
}
.footer__link::before {
  content: "";
  position: absolute;
  transition: background-color 0.3s ease;
}
@media (max-width: 767px) {
  .footer__link {
    font-size: 16px;
  }
}
.footer__link:hover:not([disabled])::before {
  bottom: -7px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #ffffff;
}
.footer__link:focus-visible:not([disabled])::before {
  top: -8px;
  left: -10px;
  width: calc(100% + 21px);
  height: calc(100% + 15px);
  border: 1px solid #ffffff;
  animation: opacity 0.3s ease;
}
.footer__link:active:not([disabled])::before {
  background-color: rgba(255, 255, 255, 0);
}
.footer__link[disabled] {
  opacity: 0.4;
  pointer-events: none;
}

.social__list {
  display: flex;
  flex-wrap: wrap;
  max-width: 97px;
  margin: 0 1px 0 0;
  padding: 0;
  list-style-type: none;
  gap: 11px;
}
@media (max-width: 1199px) {
  .social__list {
    margin: 0;
  }
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 25px;
  min-height: 25px;
  color: #030a1a;
  background-color: #ffffff;
  border-radius: 50%;
  outline: none;
  transition: all 0.3s ease;
}
.social__link:hover:not([disabled]) {
  color: #ffffff;
  background-color: #103aae;
}
.social__link:focus-visible:not([disabled]) {
  color: #ffffff;
  background-color: #103aae;
}
.social__link:active:not([disabled]) {
  color: #ffffff;
  background-color: #102654;
}
.social__link[disabled] {
  opacity: 0.3;
  pointer-events: none;
}

