@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover {
  &:hover:not([disabled]) {
    @content;
  }
}

@mixin focus {
  &:focus-visible:not([disabled]) {
    @content;
  }
}

@mixin focus-within {
  &:focus-within:not([disabled]) {
    @content;
  }
}

@mixin active {
  &:active:not([disabled]) {
    @content;
  }
}

@mixin disabled {
  &[disabled] {
    @content;
  }
}

@mixin hover-focus {
  @include hover {
    @content;
  }

  @include focus {
    @content;
  }
}

@mixin focus-within-active {
  @include focus-within {
    @content;
  }

  @include active {
    @content;
  }
}

@mixin hover-focus-active {
  @include hover {
    @content;
  }

  @include focus {
    @content;
  }

  @include active {
    @content;
  }
}

@keyframes opacity {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

